ul.flex-direction-nav {

    @include breakpoint(md) {

    }
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    width: 100%;
    left: 0;
    margin: 0;
    padding: 0px;
    list-style: none;
}

li.flex-nav-prev {
    float: left;
}
li.flex-nav-next {
    float: right;
}
a.flex-next {
    visibility:hidden;
    text-decoration: none;
}
a.flex-prev {
    visibility:hidden;
    text-decoration: none;
}
a.flex-next::after {
    visibility:visible;
    content: "\f345";
    font-family: dashicons;
    color: $gray;
    margin-right: .5rem;
    font-size: 4rem;
    @include breakpoint(sm) {
        font-size: 7rem;
    }
}
a.flex-prev::before {
    visibility:visible;
    content: "\f341";
    font-family: dashicons;
    color: $gray;
    margin-left: .5rem;
    font-size: 4rem;
    @include breakpoint(sm) {
        font-size: 7rem;
    }
}

.flex-control-paging {
    position: absolute;
    bottom: -3rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    display: flex;
}

.flex-control-paging li a {
    width: 1.4rem;
    height: 1.4rem;
    @include breakpoint(md) {
        width: 2rem;
        height: 2rem;
    }
    display: block;
    background: $border;
    cursor: pointer;
    text-indent: -9999px;
    border-radius: 2rem;
    box-shadow: inset 0 0 3px rgba($dark,0.3);
    margin: 0 .5rem;
    outline: none;
}
.flex-control-paging li a:hover {
    background: rgba( $dark, 0.2 );
}
.flex-control-paging li a.flex-active {
    background: $gray;
    cursor: default;
}

